html,body {
  height: 100%;
}
body {
  min-height: 100%;
  position:relative;
  overflow:hidden;
  margin: 0;
  padding: 0;
}
.circle{
  position: relative;
  -moz-transition: opacity 1s;
  -webkit-transition: opacity 1s;
  -ms-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
}
#svg1{
  position: relative;
}
.h-65{
height:65% !important;
}
#home_carousel_container{
  position: absolute;
}
.home-text-container{
  position: absolute;
  bottom: 10%;
  border: 1px solid #2e4f57;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;

}
